* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  display: flex;
  max-height: 45px;
  width: 11%;
  background-color: #2f324f;
  color: #0099ff;
  border: 6px outset #ffa500;
  padding: 5px 20px;
  text-align: center;
  margin-top: 20px;
}

.timer .label {
  position: absolute;
  visibility: hidden;
}

.timer .value {
  margin-right: 20px;
}

.field {
  position: relative;
  text-align: left;
}
.field:not(:last-child)::before {
  display: block;
  content: ':';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  font-size: 14px;
  padding-bottom: 2px;
  padding-left: 15px;
}
